import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { nanoid } from '@reduxjs/toolkit';
import '../../main.css';
import { setGene } from "../assetChooser/assetSlice";

export const PlotViewer = () => {
    const options = useSelector(state => state.assets)
    const allowedGenes = useSelector(state => state.geneList.geneList)
    const dispatch = useDispatch()
    const [search, setSearch] = useState("")
    const [displayedItems, setDisplayedItems] = useState(1001)
    const [flashing, setFlashing] = useState("")

    const modifiedGene = options.gene.replace(";", "%3B")

    const imageURL1 = ["aCD4", "EP", "Ery", "FoeT", "Mac0"].includes(options.cellType1) ?
        "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage/main/"+options.cellType1+"/"+modifiedGene+".jpg" :
        (["Mac1", "Mac2", "MK", "Mon", "naCD4", "nB"].includes(options.cellType1) ?
            "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage2/main/"+options.cellType1+"/"+modifiedGene+".jpg" :
                "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage3/main/"+options.cellType1+"/"+modifiedGene+".jpg"
        )

    const imageURL2 = ["aCD4", "EP", "Ery", "FoeT", "Mac0"].includes(options.cellType2) ?
        "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage/main/"+options.cellType2+"/"+modifiedGene+".jpg" :
        (["Mac1", "Mac2", "MK", "Mon", "naCD4", "nB"].includes(options.cellType2) ?
                "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage2/main/"+options.cellType2+"/"+modifiedGene+".jpg" :
                "https://raw.githubusercontent.com/RohanMalikRCDS/GenomeWideContactPlotStorage3/main/"+options.cellType2+"/"+modifiedGene+".jpg"
        )

    function imageExists(imageURL) {
        let http = new XMLHttpRequest();
        http.open('HEAD', imageURL, false);
        http.send();
        return http.status !== 404;
    }

    function subGeneStartsWith(geneComposite, searchStr) {
        if (geneComposite.startsWith(searchStr)) { return true }
        const subGenes = geneComposite.split(";")
        return subGenes.some(subGene => subGene.startsWith(searchStr))
    }

    if (options.gene !== "ERROR3" && (!imageExists(imageURL1) || !imageExists(imageURL2))) {
        dispatch(setGene("ERROR"))
    }



    function getNumGenes() {
        if (search === "") { return 20701; }
        else { return allowedGenes.filter(gene => subGeneStartsWith(gene.toLowerCase(), search.toLowerCase())).length }
    }

    function setClipboard(data) {
        navigator.clipboard.writeText(data)
            .then(() => { console.log("Set clipboard") })
            .catch(() => { console.log("Error") })
        setFlashing(data)
        console.log("flashing on")
        setTimeout(() => { setFlashing(""); console.log("flashing off") }, 750)
    }

    const geneTableRows = []
    if (search === "") {
        for (let i = displayedItems-1001; i < displayedItems; i += 10) {
            const row = <tr>
                {allowedGenes.slice(i, i + 10).map(geneName => {
                    if (flashing === geneName) {
                        return (<td onClick={() => setClipboard(geneName)} key={nanoid()} className="tdAnim"
                                    style={{fontSize: "0.85rem", textAlign: "center", overflow: "hidden"}}>{geneName}</td>)
                    } else {
                        return (<td onClick={() => setClipboard(geneName)} key={nanoid()}
                                    style={{fontSize: "0.85rem", textAlign: "center", overflow: "hidden"}}>{geneName}</td>)
                    }
                })}
            </tr>
            geneTableRows.push(row)
        }
    } else {
        const filteredGenes = allowedGenes.filter(gene => subGeneStartsWith(gene.toLowerCase(), search.toLowerCase()))
        for (let i = displayedItems-1001; i < Math.min(displayedItems, filteredGenes.length); i += 10) {
            const increment = i + 10 <= filteredGenes.length ? 10 : filteredGenes.length - i
            const row = <tr>
                {filteredGenes.slice(i, i + increment).map(geneName => {
                    if (flashing === geneName) {
                        return (<td onClick={() => setClipboard(geneName)} key={nanoid()} className="tdAnim"
                                    style={{fontSize: "0.85rem", textAlign: "center", overflow: "hidden"}}>{geneName}</td>)
                    } else {
                        return (<td onClick={() => setClipboard(geneName)} key={nanoid()}
                                    style={{fontSize: "0.85rem", textAlign: "center", overflow: "hidden"}}>{geneName}</td>)
                    }
                })}
            </tr>
            geneTableRows.push(row)
        }
    }

    return (
        <>
            { (options.gene !== "ERROR" && options.gene !== "ERROR3") &&
                <div className="flex-row" style={{width: "100%", marginTop: "0.5rem", marginLeft: "0.5rem"}}>
                    <h2 style={{
                        color: "antiquewhite",
                        marginTop: "19%",
                        marginRight: "10px"
                    }}>{options.cellType1}</h2>
                    <img style={{width: "38%", height: "38%", margin: "10px", borderRadius: "10px"}}
                         src={imageURL1}
                         alt=""/>
                    <img style={{width: "38%", height: "38%", margin: "10px", borderRadius: "10px"}}
                         src={imageURL2}
                         alt=""/>
                    <h2 style={{
                        color: "antiquewhite",
                        marginTop: "19%",
                        marginLeft: "10px"
                    }}>{options.cellType2}</h2>
                </div>
            }
            { (options.gene === "ERROR" || options.gene === "ERROR3") &&
                <div style={{display: "flex", flexDirection: "column", justifyContent: "center", alignContent: "center"}}>
                    <h3 style={{color: "antiquewhite"}}>{options.gene !== "ERROR3" ? "That gene isn't available in at least one of the requested cell types. Search for another gene below (click to copy the gene name), or choose different cell types and reselect your gene." : "Find a gene to examine with the tool below (click to copy the gene name)."}</h3>

                    <div className="flex-row">
                        <input type="text" placeholder="Start typing a gene to search" onChange={(e) => {setSearch(e.target.value); setDisplayedItems(1001)}}
                               style={{color: "antiquewhite", width: "20%", alignSelf: "center"}}/>
                        <button style={{marginLeft: "8px"}} onClick={() => setSearch("")}>Clear</button>
                    </div>
                    <div className="flex-row">
                        <button disabled={displayedItems === 1001} onClick={() => setDisplayedItems(displayedItems-1000)}
                                style={displayedItems === 1001 ? {marginTop: "1%", marginBottom: "1%", marginRight: "10%", backgroundColor: "gray", borderColor: "gray"} : {marginTop: "1%", marginBottom: "1%", marginRight: "10%"}}>Previous 1000 items</button>
                        <h5 style={{color: "antiquewhite"}}>Available Genes (showing {displayedItems-1000} to {displayedItems-1})</h5>
                        <button disabled={displayedItems > getNumGenes()} onClick={() => setDisplayedItems(displayedItems+1000)}
                                style={displayedItems > getNumGenes() ? {marginTop: "1%", marginBottom: "1%", marginLeft: "10%", backgroundColor: "gray", borderColor: "gray"} : {marginTop: "1%", marginBottom: "1%", marginLeft: "10%"}}>Next 1000 items</button>
                    </div>
                    <table style={{width: "94%", marginLeft: "3%", marginRight: "3%", tableLayout: "fixed", wordBreak: "break-all"}}>
                        <thead><tr><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th></tr></thead>
                        <tbody>{geneTableRows}</tbody>
                        <tfoot><tr><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th></tr></tfoot>
                    </table>
                </div>
            }
        </>
    )
}
