import { createSlice } from '@reduxjs/toolkit'

let geneReq = new XMLHttpRequest()
geneReq.open('GET', process.env.PUBLIC_URL+"/geneList/genes.txt", false)
geneReq.send()
const allowedGenes = geneReq.responseText.split(", ").map(val => val.substring(0, val.length-4))

const initialState = {"geneList": allowedGenes}

const geneListSlice = createSlice({
    name: "geneList",
    initialState: initialState,
    reducers: {
        setGeneList(state, action) { state.geneList = action.payload }
    }
})

export const { setGeneList } = geneListSlice.actions

export default geneListSlice.reducer